import React, { Component } from 'react';
import { withRouter } from 'react-router-dom'
import ClientService from '../../../../services/ClientService'
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import { initialState } from '../../../../constants.js';
import { UserDetail } from '../../Client/UserDetail'
import  ClientLoan  from './ClientLoan'
import {Button, Modal } from '@material-ui/core';
import moment from 'moment';
import EditProfile from './EditProfile'


const styles = theme => ({
    button: {
        fontSize: 12,
        margin: 0,
        fontWeight: 600,
        textDecoration: 'none',
        textTransform: 'none',
        borderRadius: 2,
        color: 'rgb(145, 145, 145)',
    },
    name: {
        width: 440
    },
    citizenInfo: {
        width: 300
    },
    container: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 330,
    },
    formControl: {
        margin: theme.spacing.unit,
    },
    paper: {
        position: 'absolute',
        maxWidth: 1000,
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing.unit * 4,
        outline: 'none',
        maxHeight: '100%',
        overflowY: 'auto'
    },
});

function getModalStyle() {
    const top = 50;
    const left = 50;
  
    return {
      top: `${top}%`,
      left: `${left}%`,
      transform: `translate(-${top}%, -${left}%)`,
    };
  }


class Client extends Component {
    constructor(props) {
        super(props);
        this.state = initialState
        this.service = new ClientService();
    }


    componentDidMount = () => {
        let { clientId } = this.props.match.params
    
        this.service.getClient(clientId)
            .then( response => {
                this.setState({client: response})
            })
    }



    handleOpen = () => {
        this.setState({ open: true });
      };
    
      handleClose = (status) => {
        this.setState({ open: false, editStatus: status});
      };


    render() {
        
        let { client } = this.state
        let { classes } =  this.props
        
        return (
            <div className="main-holder">
                <div className="profile-actions">
                    <Button variant="outlined" onClick={this.handleOpen} className={classes.button}>
                        Editar perfil
                    </Button>
                    <Modal
                    aria-labelledby="simple-modal-title"
                    aria-describedby="simple-modal-description"
                    open={this.state.open}
                    onClose={this.handleClose}
                    >
                    <div style={getModalStyle()} className={classes.paper}>
                        <EditProfile details={client} closeModal={this.handleClose}/>
                    </div>
                    </Modal>
                </div>
                {this.state.editStatus === "updated" ?
                            (<div className="alert alert-success" role="alert">
                                Operación realizada exitosamente!
                        </div>) : this.state.editStatus === "failed" ?(<div className="alert alert-danger" role="alert">
                                La operación a fallado.
                        </div>) : ""
                }
                <div className="client-header">
                    <div className="formHeader">
                        <h1>{client.firstName ? (client.firstName+" "+client.lastName).toUpperCase() : "Loading..."}</h1>
                    </div>
                </div>
                <div className="user-global-information">
                    <div className="user-information">
                        <div className="user-profile-holder">
                            <div className="section-header">CONTACTO</div>
                            <div >
                                <UserDetail detail={"Email"} value={client.email} />
                                <UserDetail detail={"Número de teléfono"} value={client.cellPhoneNumber} />
                                <UserDetail detail={"Dirección"} value={client.street+", "+client.residenceName+", "+client.residenceNumber+", "+client.city+", "+client.country} />                            
                            </div>
                        </div>
                        <div className="user-profile-holder">
                            <div className="section-header">PERFIL</div>
                            <div >
                                <UserDetail detail={"Nombre"} value={client.fullName} />
                                <UserDetail detail={"Lugar y fecha de nacimiento"} value={client.placeOfBirth+", "+moment(client.DOB).format("DD-MMM-YY")} />
                                <UserDetail detail={"Genero"} value={client.gender} />
                                <UserDetail detail={"Estatus civil"} value={client.civilStatus} />
                                <UserDetail detail={"DNI / Cédula / Pasaporte"} value={client.nationalId} />
                                <UserDetail detail={"Nacionalidad"} value={client.nationality} />
                                <UserDetail detail={"Otras nacionalidades"} value={client.otherNationalities} />
                                <UserDetail detail={"Certificado Migratorio"} value={client.immigrationCertificate} />
                                <UserDetail detail={"Nivel educativo"} value={client.academicLevel} />
                            </div>
                        </div>
                        <div className="user-profile-holder">
                            <div className="section-header">LABORAL</div>
                            <div >
                                <UserDetail detail={"Estatus"} value={client.employmentStatus} />
                                <UserDetail detail={"Ingresos"} value={client.monthlyIncome} />
                                { client.otherIncome && <UserDetail detail={"Otros ingresos"} value={client.otherIncome} />}
                                <UserDetail detail={"Empresa"} value={client.businessName} />
                                <UserDetail detail={"Posición"} value={client.businessPosition} />
                                <UserDetail detail={"Email"} value={client.businessEmail} />
                                <UserDetail detail={"Dirección"} value={client.businessAddress} />
                            </div>
                        </div>
                    </div>
                    <div className="client-loan-information"> 
                        { client.loans && <ClientLoan data={client.loans} clientId={client._id}/>}
                    </div>
                </div>
            </div>
 
        )
    }
}


Client.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(withRouter(Client));