import React, { Component } from 'react'
import LoanService from '../../../services/LoanService'
import TransactionService from '../../../services/TransactionService'
import moment from 'moment'
import PortfolioCategory from './PortfolioCategory'
import PortfolioCategoryItem from './PortfolioCategoryItem'

export default class  Dasboard extends Component {
    state = {
        loans: [],
        getLoan: true,
        startDate: moment().startOf('month').toISOString().substring(0, 10),
        endDate: moment().endOf('month').toISOString().substring(0, 10),
        country: 'Todos',
        countries: ['Todos', 'Venezuela', 'Peru'],
      }
      service = new LoanService();
      transactionService = new TransactionService();

      handleChange = (event) => {
        const { name, value } = event.target;
        this.setState({
          [name]: value
        })
      }
    
    
      componentDidMount = () => {
        this.fetchLoans()
        this.fetchAccountsTotals()
      }

      fetchAccountsTotals(){
        if( !this.state.accounts ){
            let country = this.props.userLocation
            console.log(country)
            this.transactionService.getInvestorsAvailability(country)
          .then(response =>{
            let total = response.reduce( (acc, e) => { return acc + e.accumTotal}, 0)
            this.setState({
              total: total
            }) 
          })
          .catch( err =>{
            this.setState({
              total:  false
            }) 
          })
        }
      }
    
      fetchLoans(e) {

          if (e) {e.preventDefault(e)}
          const {startDate, endDate} = this.state
          const { userLocation } = this.props
          this.service.getPortfolioStatus(userLocation, startDate, endDate)
          .then( async response => {
            let loans = response
            console.log(loans.portfolio)
            this.setState({
              getLoan: false,
              portfolio: loans.portfolio,
              paid: loans.paid,
              due: loans.due,
              overdue: loans.overdue,
            })
          })
          .catch(err => {
              this.setState({
                loans: false
            })
          })
      }

    render () {

        const { portfolio, overdue, due, paid, total} = this.state;
    
        return (
            <div className="content">
                <div>
                    {portfolio ? (<PortfolioCategory data={portfolio} title={'Pagos - Portafolio'} cash={total}/> ) : ""}
                    {overdue ? (
                      <div>
                        <PortfolioCategory data={overdue} title={'Pagos atrasados'}/>
                        {overdue.installments.map( e => <PortfolioCategoryItem data={e}/>)}
                    </div>) : ""}
                    {due ? (
                      <div>
                        <PortfolioCategory data={due} title={'Pagos pendientes'}/>
                        {due.installments.map( e => <PortfolioCategoryItem data={e}/>)}
                    </div>) : ""}
                    {paid ? (
                      <div>
                        <PortfolioCategory data={paid} title={'Pagos realizados'}/>
                        {paid.installments.map( e => <PortfolioCategoryItem data={e}/>)}
                    </div>) : ""}
                </div>
            </div>
        )

    }
    

} 