import axios from 'axios';
require('dotenv').config();

class ClientService {
  constructor() {
    this.service = axios.create({
      baseURL: `${process.env.REACT_APP_API_URL}/api/test/client`,
      withCredentials: true,
    });
  }

  
  updateAccount = (_id, firstName, lastName, nationalId, cellphoneNumber, city, zipCode, street, email, bank, accountNumber, monthlyIncome) => {
    console.log(_id)
    return this.service.patch(`/update/${_id}`,
      {firstName, lastName, nationalId, cellphoneNumber, city, zipCode, street, email, bank, accountNumber, monthlyIncome})
    .then(response => response.data)
  }

  createAccount = (fullName, firstName, lastName, gender ,civilStatus ,nationalId ,nationality ,otherNationalities ,placeOfBirth ,DOB ,immigrationCertificate ,academicLevel ,Occupation ,spouseFullName ,spouseNationalId ,spouseDOB ,spousePlaceOfBirth ,street ,residenceName ,residenceNumber ,city ,country ,cellphoneNumber ,email ,employmentStatus ,businessName ,businessAddress ,businessPosition ,startDate ,businessPhoneNumber ,businessEmail ,monthlyIncome ,otherIncome, borrowerStatus) => {
    return this.service.post(`/`,
      {
        fullName,
        firstName, 
        lastName,
        gender ,
        civilStatus ,
        nationalId ,nationality ,
        otherNationalities ,
        placeOfBirth ,
        DOB ,
        immigrationCertificate ,
        academicLevel ,
        Occupation ,
        spouseFullName ,
        spouseNationalId ,
        spouseDOB ,
        spousePlaceOfBirth ,
        street ,
        residenceName ,
        residenceNumber ,
        city ,
        country ,
        cellphoneNumber ,
        email ,
        employmentStatus ,businessName ,
        businessAddress ,
        businessPosition ,
        startDate ,
        businessPhoneNumber ,
        businessEmail ,
        monthlyIncome ,
        otherIncome,
        borrowerStatus
      })
    .then(response => response.data)
  }

  getClients = (country, firstName) => {
    return this.service.get(`/all-clients/${country}/${firstName}`,)
    .then(response => response.data)
  }

  getClient = (clientId) => {
    return this.service.get(`/detail/${clientId}`,)
    .then(response => response.data)
  }
  
  getInvAccounts = () => {
    return this.service.get('/get-accounts',)
    .then(response => response.data)
  }

}

export default ClientService;